import './App.css';
import Header from './Header';
import About from './About';
import Sidebar from './Sidebar';
import News from './News';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Courses from './Courses';
import FooterComponent from './FooterComponent';
import FullAboutText from './FullAboutText';
import ScrollToTop from './ScrollToTop';
import CoursesExpand from './CoursesExpand';
import Modulistica from './Modulistica';

function App() {
  const navbarItems = [
    { text: "Homepage", icon: "home", to: "/" },
    { text: "Gare ed esibizioni", icon: "medal", to: "/" },
    { text: "Modulistica", icon: "book", to: "/modulistica" }
  ];

  return (
    <Router>
      <ScrollToTop/>
      <Sidebar items={navbarItems} />
      <Routes>
        <Route path="/" element={
          <>
            <Header titolo={"LibertasViva"} sottotitolo={"A.S.D. Borgaro"} pulsante={"Scopri i nostri corsi"} link={"/"} />
            <About />
            <Courses />
            <News />
          </>
        } />
        <Route path="/FullAboutText" element={
          <>
            <Header titolo={"Chi siamo"} sottotitolo={"LibertasViva"} />
            <FullAboutText />
          </>
        } />
        <Route path="/course/:courseId" element={
          <>
            <Header titolo={"I nostri corsi"} sottotitolo={"Nel dettaglio"} />
            <CoursesExpand />
          </>
          } />
          <Route path="/modulistica" element={
          <>
            <Header titolo={"Modulistica"} sottotitolo={"Visualizza la modulistica"} />
            <Modulistica></Modulistica>
          </>
          } />
      </Routes>
      <FooterComponent />
    </Router>
  );
}

export default App;