import { Container } from "react-bootstrap";
import { Link } from "react-router-dom";

export default function About() {

    return (
        <Container className="my-5 px-3">
            <h2 className="display-2 title yellow mb-0">Chi siamo</h2>
            <p className="mt-0 mb-4 lead text-light opacity-75 readability">
                Dal 1997 l'associazione Libertas Viva Borgaro si dedica con entusiasmo allinsegnamento della ginnastica artistica ai piccoli atleti di Borgaro e dintorni. Offriamo corsi di base per bambini dai cinque anni e corsi avanzati per la preparazione agonistica. Affiliati alla F.G.I., partecipiamo a gare federali con ottimi risultati a livello nazionale e regionale. Se desideri supportarci come sponsor, contattaci al <span className="yellow">349.8052343</span>.
            </p>
            <Link to={`/FullAboutText`} className={`px-3 py-2 text-dark rounded-2 text-decoration-none bg-accent fw-bold`}>
                Scopri di pi
            </Link>
        </Container>
    );
}