import { Container } from "react-bootstrap";

export default function FullAboutText() {
    return (
        <Container className="my-5">
            <p className="lead text-light opacity-75 readability">
            Dal 1997, l'associazione Libertas Viva Borgaro si dedica con passione all'insegnamento della ginnastica artistica ai giovani di Borgaro e dintorni. Offre corsi di base per bambini dai cinque anni e corsi avanzati per atleti agonisti. Affiliata alla F.G.I., partecipa a gare federali con ottimi risultati regionali e nazionali. L'associazione offre visibilit agli sponsor e invita le aziende a contribuire al loro successo contattandoli al numero 349.8052343 o via email.<br></br><br></br>

            La sezione di ginnastica maschile, inizialmente allenata dal dott. Armando Barchi, ha affrontato difficolt logistiche, ma i giovani atleti continuano a competere, con uno di loro che ha vinto il primo posto al Campionato Regionale CSEN il 18 febbraio 2024. La direttrice tecnica, Eleonora Tancredi, premiata per i suoi meriti sportivi nel 2022, coordina le attivit con il supporto di tecnici qualificati.<br></br><br></br>

            Dal 2013, l'associazione ha introdotto un corso di ginnastica ritmica, seguito da Gabriella Mannarino, che ha portato le ginnaste a notevoli successi. Con circa 190 iscritti nel 2024, l'associazione organizza corsi a Borgaro Torinese e Mappano, partecipando a competizioni e manifestazioni comunali. L'impegno dell'associazione  stato riconosciuto dal Comune di Borgaro Torinese con una targa nel 2023.<br></br><br></br>

            I corsi si svolgono da settembre a giugno. Affiliata a F.G.I., LIBERTAS, CSEN, e EST, l'associazione ha affrontato cambiamenti amministrativi significativi a causa delle nuove normative sportive, che hanno aumentato le difficolt finanziarie senza modificare le quote di partecipazione ai corsi.
            </p>
        </Container>
    );
}
