import { Container, Row, Col, Card, Button } from 'react-bootstrap';
import tesseramento from "./assets/tesseramento.pdf";
import privacy from "./assets/privacy.pdf";

const Modulistica = () => {
    const modules = [
        {
            title: "Modulo di tesseramento",
            description: "Modulo di iscrizione per la stagione sportiva 2024.",
            date: "03/09/2024",
            link: tesseramento
        },
        {
            title: "Modulo d'informativa privacy",
            description: "Deliberatoria ai sensi del Regolamento Europeo 679/2016",
            date: "03/09/2024",
            link: privacy
        }
        ];
    
    return (
        <Container className="my-4">
            <h1 className='title yellow'>Modulistica</h1>
            <p className='text-light opacity-75'>Qui puoi trovare tutti i moduli necessari per la tua partecipazione alle attivit sportive.</p>
            <Row className="g-4"> {/* Use gutter to manage spacing between columns */}
            {modules.map((mod, index) => (
                <Col key={index} md={6}>
                <Card className='bg-main'>
                    <Card.Body>
                    <Card.Title className='yellow'>{mod.title}</Card.Title>
                    <Card.Text className='text-light opacity-75'>{mod.description}</Card.Text>
                    <Card.Text className='text-light'><strong>Data di pubblicazione:</strong> {mod.date}</Card.Text>
                    <Button className='bg-accent border-0 text-dark fw-bold' href={mod.link} download>
                        Scarica Modulo
                    </Button>
                    </Card.Body>
                </Card>
                </Col>
            ))}
            </Row>
        </Container>
    );
};

export default Modulistica;