import { Container } from "react-bootstrap";
import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

function Notizia({ pagina, titolo, testo }) {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
        <div className="pb-3 my-3 rounded-3">
            <p className="lead text-light opacity-75 fw-bold mb-0">{pagina}</p>
            <p className="small text-light opacity-50">Per leggere la notizia cliccare il pulsante qui sotto</p>
            <Button className="bg-accent border-0 text-dark fw-bold" onClick={handleShow}>
                Apri
            </Button>
        </div>
            <Modal
                size="xl"
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>{titolo}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {testo}
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={handleClose} className="bg-accent border-0 text-dark fw-bold">Chiudi</Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default function News() {
    const notizie = [
        {
            pagina: "Articolo tratto da Pagina FB CSEN VERCELLI",
            title: "CSEN VERCELLI",
            description: `
                Si  svolta il 14 e 15 Marzo scorso, la prima prova del Campionato Regionale Csen Piemonte di Ginnastica Artistica.
                
                La ginnastica Csen in Piemonte sta crescendo. Questanno gli atleti in gara hanno sfiorato i 300, quasi il 50% in pi dello scorso anno. Afferma la prof.ssa Lisa Ballarini, responsabile della ginnastica artistica CSEN del Piemonte. Un evento di grande risalto che ha visto partecipare atleti provenienti da tutto il Piemonte. Daltronde gi il corso per tecnici e quello per giudici nei mesi scorsi aveva avuto numerosi partecipanti e ci aveva fatto ben sperare.
                
                La manifestazione, che si  svolta a Gattinara nella palestra di via San Rocco,  stata da subito un successo, vera dimostrazione di forza numerica e di qualit tecniche oramai consuete nellambito della ginnastica CSEN piemontese, che negli ultimi campionati nazionali ha dimostrato di essere ai vertici della disciplina, piazzando atleti sul podio in tutte le categorie.
                
                In aumento anche la partecipazione nel settore maschile dove la parte del leone lhanno fatto gli atleti della Libertas Galliate, seguiti dal loro tecnico Musatelli Giovanni. Di rilievo il senior Stefan Sipos che si  aggiudicato il 1 posto con punteggi tutti superiori a 12.00.
                
                7 squadre partecipanti: ASD Mgco3 Gym, ASD CSEN Caselle, ASD Libertas Galliate, ASD La Marmora, ASD Libertas Viva Borgaro, ASD Arsgymnica Concertosport e la new entry Virtus Grugliasco.
                
                Questultima societ, allenata dalla tecnica Francesca Demaria, ha vinto la gara di specialit femminile, gara riservata alle atlete pi esperte, nelle categorie esordienti con Stella Silvia e nella categoria allieve con Donvito Elisa la CUP individuale senior con Neirotti Ilaria. Sempre in specialit nelle categorie junior con Salvati Marika e nelle senior con Patanella Alessia la vittoria se la aggiudica la societ Viva Borgaro, allenata da Eleonora Tancredi, che vince anche nella CUP individuale vince nella categoria junior con Castrale Carola.
                
                Ottima la prova di Arsgymnica, che  sempre presente sul podio con le sue atlete e vince nella categoria allieve CUP individuale con Galvagno Clara e piazza nella categoria esordienti le sue atlete tutte sul podio Modena, Ariotti e Calabrese rispettivamente prima, seconda e terza.
                
                Le atlete dellassociazione La Marmora sezioni di Cossato e Trivero- hanno disputato una buona prova, soprattutto alle parallele, e hanno buone possibilit d classificarsi per la fase nazionale agli attrezzi.
                
                Le atlete di casa hanno ben figurato: le piccole Morisano, Varvarych e Brera vincono nelle categoria esordienti CUP a squadre. Una buona prova anche per Piantanida Camilla e Giulia e Armitano Elisa nella stessa categoria. Le allieve conquistano un 8 posto, ma la stessa squadra formata da Sezzano Giulia, Greta Sarasso e Delmastro Diana sale sul podio al 3 posto nella gara di acrobatica. Martini Francesca nella categoria allieve sale sul secondo gradino del podio nella CUP individuale, mentre Sezzano Elena  settima nella categoria Junior. Myriam Bertolo nella stessa gara, categoria senior, conquista un ottimo terzo posto.
                
                Elisa Piga, senior e unica atleta a disputare la gara di specialit per la societ padrona di casa, sale sul 2 gradino del podio con ottimi parziali agli attrezzi soprattutto in trave.
                
                Da sottolineare che per la prima volta in Piemonte viene disputato anche il campionato di Eccellenza. Sono solo due le atlete una senior, Mariavittoria, per lMGCO3 GYM, e Salidu Elisa, per Viva Borgaro, che ci hanno fatto vedere una buonissima ginnastica ad alto livello.
                
                La 2 prova che si svolger Sabato 18 e Domenica 19 Aprile a Torino dar ai ginnasti la possibilit di migliorare il loro punteggio per ottenere l'accesso alla fase nazionale che si terr a Bellaria (RN) il 29-30-31 maggio e 1-2 giugno 2015.`,
            href: "#"
        },
        {
            pagina: "Articolo tratto dal giornale la nuova voce",
            title: "La nuova voce",
            description: `
                Domenica di gare al Palazzetto dello Sport di Borgaro Torinese. Molti atleti e atlete impegnati nelle gare di ginnastica artistica e ritmica e grandi soddisfazioni per le ginnaste della Libertas Viva e gli atleti torinesi della Asd Oxalis. Grandi e piccini che hanno fatto incetta di medaglie, davanti ad un gremito pubblico. Una domenica che ha dato vita a una grande festa di sport per i giovani atleti che hanno gareggiato.`,
            href: "#"
        },
    ];

    return (
        <Container className="mb-5 my-md-5 px-3">
            <h2 className="display-2 title yellow">Le ultime notizie</h2>
            {notizie.map((notizia, index) => (
                <Notizia
                    key={index}
                    pagina={notizia.pagina}
                    titolo={notizia.title}
                    testo={notizia.description}
                />
            ))}
        </Container>
    );
}