import React from 'react';
import { Container, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";

const courses = [
{
    image: "https://wips.plug.it/cips/sport.virgilio.it/cms/2021/07/simone-biles_66ziw4rudvta1xfbr93196x0k.jpg",
    title: "Ginnastica artistica",
    description: "Unisciti al nostro corso di Ginnastica Artistica e scopri l'eleganza e la potenza di questa disciplina sportiva! Il corso  aperto a tutte le et e livelli di abilit, offrendo un programma completo che combina flessibilit, forza, coordinazione e tecnica.",
    link: "/course/ginnastica-artistica"
},
{
    image: "https://img.freepik.com/free-photo/young-female-athlete-rhythmic-gymnastics-artist-dancing-training-isolated-purple-studio-background-with-neon-light-beautiful-girl-practicing-with-equipment-grace-performance_155003-34467.jpg?size=626&ext=jpg",
    title: "Ginnastica ritmica",
    description: "Scopri la grazia e l'armonia della Ginnastica Ritmica con il nostro corso dedicato, aperto a tutte le et e livelli di abilit. Questo corso combina movimenti eleganti e fluidi con l'uso di attrezzi come nastri, cerchi, palle, clavette e funi, offrendo un'esperienza unica e affascinante.",
    link: "/course/ginnastica-ritmica"
}
];

export default function Courses() {
return (
    <Container className="mt-5">
    <h2 className="display-2 title yellow mb-0">I nostri corsi</h2>
    <p className="mt-0 mb-4 lead text-light opacity-75 readability">
        La nostra associazione si impegna a impartire le seguenti tipologie di corsi per ogni livello ed et! Infatti come segue, vantiamo 5 valide offerte formative per ogni corso: <span className="yellow fw-medium">Baby Gym, Base, Pre-agonistica, Agonistica, Avanzata</span>.
    </p>
    <Row className="mt-3">
        {courses.map((course, index) => (
        <Col key={index} className="px-3 mb-5 mb-md-0" md={6}>
            <div className="object-fit-contain ratio ratio-16x9">
            <img src={course.image} className="img-fluid rounded-2 object-fit-cover" alt={course.title}></img>
            </div>
            <h6 className="display-6 title yellow my-3 mb-0">{course.title}</h6>
            <p className="mt-0 lead text-light opacity-75">{course.description}</p>
            <Link to={course.link} className="px-3 py-2 text-dark rounded-2 text-decoration-none bg-accent fw-bold">
            Scopri di pi
            </Link>
        </Col>
        ))}
    </Row>
    </Container>
);
}