import { Container, Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSquareInstagram, faSquareFacebook } from '@fortawesome/free-brands-svg-icons';
import statuto from './assets/assemblea e nuovo statuto.pdf';

const socialLinks = [
    { icon: faSquareInstagram, href: "https://www.instagram.com/libertasviva/?hl=it" },
    { icon: faSquareFacebook, href: "https://www.facebook.com/groups/133881573314468/?locale=it_IT" },
];

function FooterComponent() {

    return (
        <footer>
            <Container className='bg-main p-4 rounded-top-3'>
                <Row>
                    <Col md={4}>
                        <h2 data-aos="fade-up" className="lead title yellow">Sede Sociale</h2>
                            <p data-aos="fade-up" className="small mb-5 opacity-75 text-light">
                            Via Tibaldi 15 10079 Mappano<br></br>
                            CF 92015320010
                        </p>
                    </Col>
                    <Col md={4}>
                        <h2 data-aos="fade-up" className="lead title yellow">Sede Operativa</h2>
                        <p data-aos="fade-up" className="small mb-5 opacity-75 text-light">
                            Piazza della Repubblica c/o palestre<br></br>Scuole Elementare Defassi Villa Tapparellio
                        </p>
                    </Col>
                    <Col md={4}>
                        <h2 data-aos="fade-up" className="lead title yellow">Contatti</h2>
                        <p data-aos="fade-up" className="small mb-5 opacity-75 text-light">
                            Ref. Eleonora Tancredi: Cell. 349.80.52.343<br></br>
                            info@libertasvivaborgaro.it
                        </p>
                    </Col>
                    <Col md={12}>
                        
                        <h2 data-aos="fade-up" className="lead title yellow">Socials</h2>
                        <p data-aos="fade-up" className="small mb-2 opacity-75 text-light">
                            Seguici sui nostri social per non perderti le ultime notizie.
                        </p>
                    </Col>
                </Row>
                
                
                <div data-aos="fade-up" className="d-flex">
                    {socialLinks.map((link) => (
                        <a key={link.href} className="pe-3 text-decoration-none text-light opacity-75 lead" href={link.href} target='_blank' rel='noreferrer'>
                            <FontAwesomeIcon icon={link.icon} />
                        </a>
                    ))}
                </div>

                <p className='small text-light opacity-75 mt-5'>Per consultare lo Statuto e Regolamento cliccare <span className='text-decoration-underline'><a className='yellow' target='_blank' href={statuto}>qui</a></span><br></br>
                Copyright  2024 LibertasViva, tutti i diritti sono riservati</p>
            </Container>
        </footer>
    );
}

export default FooterComponent;
