import React from 'react';
import { Container, Row, Col, Table } from 'react-bootstrap';
import { useParams } from 'react-router-dom';

const gymnasticsCourses = {
"ginnastica-artistica": {
    title: "Ginnastica Artistica Femminile e Maschile",
    description: `
    Unisciti al nostro corso di Ginnastica Artistica e scopri l'eleganza e la potenza di questa disciplina sportiva! Il corso  aperto a tutte le et e livelli di abilit, offrendo un programma completo che combina flessibilit, forza, coordinazione e tecnica.
    `,
    whatWeOffer: `
    Offriamo lezioni sia per principianti che per atleti avanzati, con sessioni strutturate che permettono di sviluppare le competenze fondamentali e perfezionare le tecniche pi sofisticate. I nostri istruttori qualificati, esperti nel loro campo, ti guideranno passo dopo passo, assicurandoti un apprendimento che  sia sicuro che divertente. Utilizziamo attrezzature moderne e professionali, garantendo cos unesperienza di allenamento ottimale per tutti. Infine, il nostro ambiente inclusivo  un luogo accogliente dove ogni atleta, indipendentemente dal livello, pu crescere e migliorare.
    `,
    benefits: `
    Partecipare alle nostre lezioni ti porter numerosi vantaggi, tra cui un significativo sviluppo fisico: potrai migliorare la tua forza, flessibilit e coordinazione. Inoltre, imparerai valori importanti come la disciplina, la resilienza e il lavoro di squadra, che ti accompagneranno anche al di fuori dell'allenamento. Non solo, avrai l'opportunit di divertirti e socializzare, incontrando nuove persone e prendendo parte a eventi e competizioni emozionanti.
    `,
    schedule: [
    { day: "Marted", time: "17:30 - 20:00" },
    { day: "Mercoled", time: "17:30 - 20:00" },
    { day: "Venerd", time: "17:30 - 20:00" },
    { day: "Sabato", time: "10:00 - 12:30" }
    ],
    location: `Palestra "Scuola Elementare DEFASSI", Entrata da piazza della Repubblica, portone Verde`
},
"ginnastica-ritmica": {
    title: "Ginnastica Ritmica",
    description: `
    Scopri la grazia e l'armonia della Ginnastica Ritmica con il nostro corso dedicato, aperto a tutte le et e livelli di abilit. Questo corso combina movimenti eleganti e fluidi con l'uso di attrezzi come nastri, cerchi, palle, clavette e funi, offrendo un'esperienza unica e affascinante.
    `,
    whatWeOffer: `
    Nel nostro programma di ginnastica ritmica, proponiamo lezioni strutturate sia per principianti che per atleti avanzati, consentendo a tutti di apprendere le basi e perfezionare le tecniche pi sofisticate. I nostri istruttori, esperti e certificati, ti guideranno con professionalit, assicurandoti un apprendimento che  non solo sicuro, ma anche divertente. Utilizziamo attrezzature moderne e professionali, che garantiscono un allenamento di alta qualit. Inoltre, il nostro ambiente inclusivo  un luogo accogliente dove ogni atleta pu esprimersi liberamente e sviluppare il proprio talento.
    `,
    benefits: `
    Partecipando alle nostre lezioni, potrai godere di numerosi benefici, a partire dallo sviluppo fisico: migliorerai la tua flessibilit, coordinazione, forza e senso del ritmo. La ginnastica ritmica  anche unarte che stimola la creativit e lespressivit, permettendoti di raccontare emozioni e storie attraverso i movimenti e le coreografie. Inoltre, imparerai valori fondamentali come la disciplina, la dedizione e il lavoro di squadra, che ti accompagneranno in ogni aspetto della vita. Infine, avrai lopportunit di divertirti e socializzare, incontrando nuove persone e partecipando a eventi e competizioni emozionanti.
    `,
    schedule: [
    { day: "Luned", time: "17:30 - 20:00" },
    { day: "Gioved", time: "17:30 - 20:00" },
    { day: "Sabato", time: "10:00 - 12:30" }
    ],
    location: `Palestra "Scuola Elementare DEFASSI", Entrata da piazza della Repubblica, portone Verde`
}
};

const CoursesExpand = () => {
const { courseId } = useParams();
const course = gymnasticsCourses[courseId];

if (!course) {
    return <div>Corso non trovato</div>;
}

return (
    <Container className="my-5">
    <Row>
        <Col>
        <h2 className="display-4 title yellow">{course.title}</h2>
        <p className="lead text-light opacity-75">{course.description}</p>
        
        <h4 className="title yellow">Cosa Offriamo</h4>
        <p className="lead text-light opacity-75">{course.whatWeOffer}</p>
        
        <h4 className="title yellow">Benefici</h4>
        <p className="lead text-light opacity-75">{course.benefits}</p>
        
        <h4 className="title yellow">Orari</h4>
        <Table striped bordered hover className="text-light" variant='dark'>
            <thead className="bg-dark text-light">
            <tr>
                <th>Giorno</th>
                <th>Orario</th>
            </tr>
            </thead>
            <tbody>
            {course.schedule.map((slot, index) => (
                <tr key={index}>
                <td>{slot.day}</td>
                <td>{slot.time}</td>
                </tr>
            ))}
            </tbody>
        </Table>
        
        <h4 className="title yellow">Indirizzo</h4>
        <p className="lead text-light opacity-75">{course.location}</p>
        </Col>
    </Row>
    </Container>
);
};

export default CoursesExpand;
